"use client"

import { useEffect, useState } from "react"
import { createClient } from "@/lib/supabase/client"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Progress } from "@/components/ui/progress"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { AlertCircle, Flame, TrendingUp, BookOpen } from "lucide-react"
import Link from "next/link"
import { playSound } from "@/lib/gamification"

interface StudentStats {
  currentLevel: number
  totalXP: number
  currentStreak: number
  averageAccuracy: number
  boardRank: number
}

interface TodayQuest {
  topicId: string
  topicName: string
  progress: number
  xpPotential: number
}

interface TrainingAlert {
  topicId: string
  topicName: string
  revisionNumber: number
  daysOverdue: number
}

interface LeaderboardEntry {
  rank: number
  name: string
  xp: number
  avatar: string
}

export function StudentHomeDashboard({ studentId }: { studentId: string }) {
  const [stats, setStats] = useState<StudentStats | null>(null)
  const [todayQuests, setTodayQuests] = useState<TodayQuest[]>([])
  const [trainingAlerts, setTrainingAlerts] = useState<TrainingAlert[]>([])
  const [leaderboard, setLeaderboard] = useState<LeaderboardEntry[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    playSound("xp")

    const fetchDashboardData = async () => {
      const supabase = createClient()

      try {
        // =============================
        // 1. Fetch Student Stats
        // =============================
        const { data: studentProfile, error: profileError } = await supabase
          .from("student_profiles")
          .select("current_level, total_points, current_streak, overall_accuracy, board_rank")
          .eq("profile_id", studentId)
          .single()

        if (profileError) throw profileError

        if (studentProfile) {
          const { data: studentProfile, error: profileError } = await supabase
  .from("student_profiles")
  .select("current_level, total_points, current_streak, overall_accuracy")
  .eq("profile_id", studentId)
  .single()

if (profileError) throw profileError

// 1b. Fetch REAL RANK from leaderboard
const { data: rankRow, error: rankError } = await supabase
  .from("leaderboard")
  .select("rank")
  .eq("student_id", studentId)
  .single()

if (rankError) {
  console.error("Rank not found, defaulting to 999")
}

const realRank = rankRow?.rank ?? 999

// Final stats assignment
if (studentProfile) {
  setStats({
    currentLevel: studentProfile.current_level || 1,
    totalXP: studentProfile.total_points || 0,
    currentStreak: studentProfile.current_streak || 0,
    averageAccuracy: studentProfile.overall_accuracy || 0,
    boardRank: realRank, // üî• REAL RANK FIXED HERE
  })
}
        }

        // =============================
        // 2. Today‚Äôs Quests
        // =============================
        const { data: topicProgressData, error: progressError } = await supabase
          .from("topic_progress")
          .select(`
            topic_id,
            progress_percentage,
            total_xp,
            topics(name)
          `)
          .eq("student_id", studentId)
          .order("last_updated", { ascending: false })
          .limit(3)

        if (progressError) throw progressError

        if (topicProgressData) {
          setTodayQuests(
            topicProgressData.map((tp: any) => ({
              topicId: tp.topic_id,
              topicName: tp.topics?.name || "Unknown Topic",
              progress: tp.progress_percentage || 0,
              xpPotential: tp.total_xp || 0,
            })),
          )
        }

        // =============================
        // 3. Revision Alerts
        // =============================
        const { data: revisionAlerts, error: alertError } = await supabase
          .from("revision_schedule")
          .select(`
            topic_id,
            revision_number,
            due_date,
            is_overdue,
            topics(name)
          `)
          .eq("student_id", studentId)
          .eq("is_completed", false)
          .eq("is_overdue", true)
          .limit(5)

        if (alertError) throw alertError

        if (revisionAlerts) {
          setTrainingAlerts(
            revisionAlerts.map((alert: any) => {
              const dueDate = new Date(alert.due_date)
              const today = new Date()
              const daysOverdue = Math.floor((today.getTime() - dueDate.getTime()) / (1000 * 60 * 60 * 24))
              return {
                topicId: alert.topic_id,
                topicName: alert.topics?.name || "Unknown Topic",
                revisionNumber: alert.revision_number,
                daysOverdue: daysOverdue,
              }
            }),
          )
        }

        // =============================
        // 4. REAL LEADERBOARD (TOP 3)
        // =============================
        const { data: leaderboardData, error: leaderboardError } = await supabase
          .from("leaderboard")
          .select("rank, student_name, total_points, avatar_url")
          .order("rank", { ascending: true })
          .limit(3)

        if (leaderboardError) throw leaderboardError

        if (leaderboardData) {
          setLeaderboard(
            leaderboardData.map((entry: any) => ({
              rank: entry.rank,
              name: entry.student_name || "Unknown",
              xp: entry.total_points || 0,
              avatar: entry.avatar_url || "üë§",
            })),
          )
        }
      } catch (error) {
        console.error("Error fetching dashboard data:", error)
        setError("Failed to load dashboard data")
      } finally {
        setLoading(false)
      }
    }

    fetchDashboardData()
  }, [studentId])

  if (loading) {
    return (
      <div className="p-8 text-center">
        <div className="inline-block">Loading...</div>
      </div>
    )
  }

  if (error) {
    return <div className="p-8 text-center text-destructive">{error}</div>
  }

  if (!stats) {
    return <div className="p-8 text-center text-muted-foreground">No profile data found</div>
  }

  const xpToNextLevel = (stats.currentLevel + 1) * 1000 - stats.totalXP
  const progressToNextLevel = (stats.totalXP % 1000) / 10

  return (
    <div className="min-h-screen bg-gradient-to-b from-background via-secondary/5 to-background">
      {/* Header Section */}
      <div className="bg-gradient-to-r from-primary/10 to-accent/10 border-b border-border">
        <div className="max-w-7xl mx-auto px-6 py-8">
          <div className="flex items-center justify-between">
            <div className="space-y-2">
              <h1 className="text-4xl font-bold text-foreground">Level Up Your UPSC Prep</h1>
              <p className="text-muted-foreground">Welcome back! {todayQuests.length} quests available today.</p>
            </div>
            <div className="text-right space-y-2">
              <div className="flex items-center gap-2">
                <span className="text-5xl font-bold text-primary">#{stats.boardRank}</span>
                <span className="text-xl font-semibold text-muted-foreground">on Leaderboard</span>
              </div>
              <div className="text-sm text-muted-foreground">Current Rank</div>
            </div>
          </div>

          {/* XP Bar */}
          <div className="mt-6 space-y-2">
            <div className="flex justify-between items-center">
              <span className="text-sm font-medium">Level {stats.currentLevel}</span>
              <span className="text-sm text-muted-foreground">{xpToNextLevel} XP to next level</span>
            </div>
            <Progress value={progressToNextLevel} className="h-3 bg-muted" />
          </div>
        </div>
      </div>

      {/* Main Content */}
      <div className="max-w-7xl mx-auto px-6 py-8">
        <div className="grid md:grid-cols-2 gap-6 mb-8">
          {/* Card 1: Today's Quests */}
          <Card className="border-2 border-primary/20 hover:shadow-lg transition-shadow">
            <CardHeader className="pb-4">
              <div className="flex items-center justify-between">
                <CardTitle className="text-xl flex items-center gap-2">
                  <BookOpen className="w-5 h-5 text-primary" />
                  Today's Quests
                </CardTitle>
                <Badge className="bg-primary/20 text-primary hover:bg-primary/30">{todayQuests.length} Available</Badge>
              </div>
            </CardHeader>
            <CardContent className="space-y-4">
              {todayQuests.length === 0 ? (
                <p className="text-muted-foreground text-sm">No quests available</p>
              ) : (
                todayQuests.map((quest) => (
                  <div
                    key={quest.topicId}
                    className="p-3 bg-muted/40 rounded-lg border border-border hover:bg-muted/60 transition-colors cursor-pointer"
                    onClick={() => playSound("xp")}
                  >
                    <div className="flex justify-between items-start mb-2">
                      <span className="font-medium text-sm">{quest.topicName}</span>
                      <Badge variant="outline" className="text-xs">
                        +{quest.xpPotential} XP
                      </Badge>
                    </div>
                    <Progress value={quest.progress} className="h-2" />
                    <div className="text-xs text-muted-foreground mt-1">{quest.progress}% Complete</div>
                  </div>
                ))
              )}
            </CardContent>
          </Card>

          {/* Card 2: Training Alerts */}
          <Card className="border-2 border-warning/20 hover:shadow-lg transition-shadow">
            <CardHeader className="pb-4">
              <CardTitle className="text-xl flex items-center gap-2">
                <AlertCircle className="w-5 h-5 text-warning" />
                Training Alerts
              </CardTitle>
            </CardHeader>
            <CardContent>
              <Badge className="bg-warning/20 text-warning hover:bg-warning/30 mb-4">
                {trainingAlerts.length} OVERDUE
              </Badge>
              <div className="space-y-2">
                {trainingAlerts.length === 0 ? (
                  <p className="text-muted-foreground text-sm">No overdue items</p>
                ) : (
                  trainingAlerts.slice(0, 2).map((alert, idx) => (
                    <div key={idx} className="p-3 bg-destructive/10 border border-destructive/30 rounded-lg text-sm">
                      <span className="font-medium">
                        {alert.topicName} Rev{alert.revisionNumber}
                      </span>
                      <div className="text-xs text-muted-foreground mt-1">
                        {alert.daysOverdue > 0 ? `${alert.daysOverdue} day(s) overdue` : "Today"} üî¥
                      </div>
                    </div>
                  ))
                )}
              </div>
            </CardContent>
          </Card>

          {/* Card 3: Your Stats */}
          <Card className="border-2 border-accent/20 hover:shadow-lg transition-shadow">
            <CardHeader className="pb-4">
              <CardTitle className="text-xl">Your Stats</CardTitle>
            </CardHeader>
            <CardContent className="space-y-3">
              <div className="grid grid-cols-3 gap-3">
                <div className="text-center p-3 bg-muted/40 rounded-lg">
                  <div className="text-2xl font-bold text-primary">{stats.currentLevel}</div>
                  <div className="text-xs text-muted-foreground">Level</div>
                </div>
                <div className="text-center p-3 bg-muted/40 rounded-lg">
                  <div className="text-2xl font-bold text-accent">{stats.totalXP}</div>
                  <div className="text-xs text-muted-foreground">Total XP</div>
                </div>
                <div className="text-center p-3 bg-muted/40 rounded-lg">
                  <div className="flex items-center justify-center gap-1 text-xl font-bold text-warning">
                    <Flame className="w-4 h-4" />
                    {stats.currentStreak}
                  </div>
                  <div className="text-xs text-muted-foreground">Streak</div>
                </div>
              </div>
              <div className="p-3 bg-muted/40 rounded-lg text-center">
                <div className="text-sm text-muted-foreground">Accuracy</div>
                <div className="text-xl font-bold text-primary mt-1">{Math.round(stats.averageAccuracy)}%</div>
              </div>
            </CardContent>
          </Card>

          {/* Card 4: Class Leaderboard Snapshot */}
          <Card className="border-2 border-accent/20 hover:shadow-lg transition-shadow">
            <CardHeader className="pb-4">
              <CardTitle className="text-xl flex items-center gap-2">
                <TrendingUp className="w-5 h-5 text-accent" />
                Class Leaderboard
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-2">
              {leaderboard.length === 0 ? (
                <p className="text-muted-foreground text-sm">No leaderboard data</p>
              ) : (
                leaderboard.map((entry) => (
                  <div
                    key={entry.rank}
                    className="flex items-center justify-between p-2 hover:bg-muted/40 rounded transition-colors"
                  >
                    <div className="flex items-center gap-3">
                      <span className="text-lg font-bold text-primary">#{entry.rank}</span>
                      <span className="text-xl">{entry.avatar}</span>
                      <span className="font-medium">{entry.name}</span>
                    </div>
                    <span className="font-bold text-accent">{entry.xp} pts</span>
                  </div>
                ))
              )}
            </CardContent>
          </Card>
        </div>

        {/* Footer Buttons */}
        <div className="flex gap-4 flex-col sm:flex-row">
          <Link href="/student/quest-log" className="flex-1">
            <Button className="w-full" size="lg" onClick={() => playSound("checkpoint")}>
              ‚Üí New Quest
            </Button>
          </Link>
          <Link href="/student/training-dojo" className="flex-1">
            <Button
              className="w-full bg-transparent"
              size="lg"
              variant="outline"
              onClick={() => playSound("checkpoint")}>
              ‚öîÔ∏è Train Now
            </Button>
          </Link>
          <Link href="/student/leaderboard" className="flex-1">
            <Button
              className="w-full bg-transparent"
              size="lg"
              variant="outline"
              onClick={() => playSound("checkpoint")}>
              üèÜ Full Leaderboard
            </Button>
          </Link>
        </div>
      </div>
    </div>
  )
}
