"use client"

import Link from "next/link"
import { usePathname } from "next/navigation"
import { LayoutDashboard, Trophy, Settings, LogOut, Zap, Scroll, Flame, Users2, BookOpen } from "lucide-react"
import { cn } from "@/lib/utils"
import { Button } from "@/components/ui/button"
import { createClient } from "@/lib/supabase/client"
import { useRouter } from "next/navigation"

export function StudentSidebar() {
  const pathname = usePathname()
  const router = useRouter()

  const handleLogout = async () => {
    const supabase = createClient()
    await supabase.auth.signOut()
    router.push("/auth/login")
  }

  const menuItems = [
    {
      icon: LayoutDashboard,
      label: "Home",
      href: "/student/dashboard",
    },
    {
      icon: Scroll,
      label: "Quest Log",
      href: "/student/quest-log",
    },
    {
      icon: Flame,
      label: "Training Dojo",
      href: "/student/training-dojo",
    },
    {
      icon: BookOpen,
      label: "Subjects",
      href: "/student/subjects",
    },
    {
      icon: Users2,
      label: "Character Sheet",
      href: "/student/character-sheet",
    },
    {
      icon: Trophy,
      label: "Leaderboard",
      href: "/student/leaderboard",
    },
    {
      icon: Settings,
      label: "Profile",
      href: "/student/profile",
    },
  ]

  return (
    <div className="hidden md:flex flex-col w-64 bg-sidebar border-r border-sidebar-border h-screen">
      <div className="p-6 border-b border-sidebar-border">
        <Link href="/student/dashboard" className="flex items-center gap-2">
          <div className="w-8 h-8 rounded-lg bg-gradient-to-r from-primary to-accent flex items-center justify-center">
            <Zap className="w-5 h-5 text-white" />
          </div>
          <div>
            <h1 className="font-bold text-sidebar-foreground">Gamify IAS</h1>
            <p className="text-xs text-sidebar-accent">Academy</p>
          </div>
        </Link>
      </div>

      <nav className="flex-1 p-4 space-y-2 overflow-y-auto">
        {menuItems.map((item) => {
          const Icon = item.icon
          const isActive = pathname === item.href || pathname.startsWith(item.href + "/")
          return (
            <Link key={item.href} href={item.href}>
              <Button
                variant={isActive ? "default" : "ghost"}
                className={cn(
                  "w-full justify-start gap-3",
                  isActive && "bg-sidebar-primary text-sidebar-primary-foreground",
                )}
              >
                <Icon className="w-4 h-4" />
                {item.label}
              </Button>
            </Link>
          )
        })}
      </nav>

      <div className="p-4 border-t border-sidebar-border space-y-2">
        <Button onClick={handleLogout} variant="outline" className="w-full justify-start gap-2 bg-transparent">
          <LogOut className="w-4 h-4" />
          Logout
        </Button>
      </div>
    </div>
  )
}
